<template>
    <div class="data-tracking">
        <tabs v-model="active" @click="selectTab">
            <tab v-for="(item, index) of tabs"
                :title="item" 
                :key="index"></tab>
        </tabs>
        <div class="router-content">
            <keep-alive>
                <router-view></router-view>
            </keep-alive>
        </div>
    </div>
</template>

<script>
import { setItem }              from '../../util/util';
import { mapGetters, mapState, mapMutations } from 'vuex'
import { Tab, Tabs }            from 'vant';
export default {
    data () {
       return {
            // active: 0,
            tabs: ['时间', '行为', '人']
       };
    },
    created() {
        this.$router.push(`${this.childrenRoute}`);
    },
    methods: {
        selectTab(index) {
            let urls = ['/data-tracking/time', '/data-tracking/actions', '/data-tracking/people'];
            setItem('path', urls[index]);
            this.$router.push(`${urls[index]}`);
            this.CHANGECHILDRENROUTE(urls[index]);
            // this.$store.commit('CHANGECHILDRENROUTE', urls[index]);
        },
        ...mapMutations(['CHANGECHILDRENROUTE'])
    },
    components: {
        Tab,
        Tabs
    },
    computed: {
        ...mapState({
            childrenRoute: 'childrenRoute'
        }),
        active: {
            get() {
                let urls = ['/data-tracking/time', '/data-tracking/actions', '/data-tracking/people'];
                return urls.indexOf(this.childrenRoute);
            },
            set() {

            }
        }
    },
    watch: {
        '$route'(to, from, next) {
            // if((from.fullPath === '/me' || from.fullPath === '/boss') && to.path !== '/codeimg' ) {
            //     console.log('from', from);
            //     console.log('to', to);
            //     let urls = ['/data-tracking/time', '/data-tracking/actions', '/data-tracking/people'];
            //     this.$router.push(`${this.childrenRoute}`);
            //     this.active = urls.indexOf(this.childrenRoute);
            // }else{
            //     // 跳到我的名片页
            //     this.$router.push(to.path);
            // }
        }
    }
}
</script>

<style lang='less' scoped>
@import url(./index.less);
</style>


